<template>
  <div>
      <v-container class="searcher-wrapper">
          <v-btn ref="search_btn"
                 @click="copyToClipboardProxy"
                 large
                 class="mr-10 d-none d-md-block"
                 :loading="copyToClipboardBtn.loading"
                 depressed tile
          >{{ copyToClipboardBtn.text }}</v-btn>
          <Searcher :searcher-string="searcherString"
                    :corpus-name="corpusName"
                    @searcher-string="onSearcherString"
          ></Searcher>
      </v-container>
      <v-container>
          <SearchResults v-if="matchingLines"
                         :lines-matching="matchingLines"
                         :searcher-string="searcherString"
                         :disable-infinite-scroll="disableInfiniteScroll"
                         ref="search_results"
          ></SearchResults>
      </v-container>
      <v-snackbar
          v-model="snackbar.isVisible"
          :color="snackbar.color"
          top left outlined
      >{{ snackbar.text }}</v-snackbar>
  </div>
</template>

<script>
import Searcher from '@/components/corpusSimple/Searcher';
import SearchResults from '@/components/corpusSimple/SearchResults';
import { sleep } from '@/services/sleep';
import copyToClipboardManyMixin from '@/mixins/copyToClipboardManyMixin';

export default {
    name: 'CorpusSearch',
    props: ['corpusName'],
    mixins: [
        copyToClipboardManyMixin,
    ],
    components: {
        Searcher,
        SearchResults,
    },
    data() {
        return {
            searcherString: '',
            disableInfiniteScroll: false,
            copyToClipboardBtn: {
                text: 'Copy to Clipboard',
                loading: false,
            },
            snackbar: {
                isVisible: false,
                text: '',
                color: 'success'
            },
        }
    },
    computed: {
        matchingLines() {
            return this.$store.getters.matchingLinesByCorpus(this.corpusName);
        }
    },
    created() {
        this.$store.commit('initializeCorpusData', this.corpusName);
        this.$store.commit('setSearcherString', {
            corpusName: this.corpusName,
            searcherString: this.searcherString,
        });

    },
    mounted() {
        if (this.$route.query.search) {
            this.onSearcherString(this.$route.query.search);
        }
    },
    watch: {
        searcherString(value, prev) {
            if (value !== prev || !value) {
                this.setQuery();
            }
        },
    },
    methods: {
        onSearcherString(searcherString) {
            this.searcherString = searcherString;
            this.$store.commit('setSearcherString', {
                corpusName: this.corpusName,
                searcherString: this.searcherString,
            });
        },
        setQuery() {
            const query = this.searcherString ? { search: this.searcherString } : {};
            if (_.isEqual(query, this.$route.query)) {
                return;
            }
            this.$router.push({ query });
        },
        async copyToClipboardProxy() {
            try {
                this.copyToClipboardBtn.loading = true;
                await sleep(100);
                this.disableInfiniteScroll = true;
                await sleep(200);
                const selector = '.search-results';
                const elem = document.querySelector(selector);
                window.getSelection().removeAllRanges()
                const selection = window.getSelection();
                selection.selectAllChildren(elem);
                document.execCommand('copy');
                this.copyToClipboardBtn.loading = false;
                selection.removeAllRanges();
                this.disableInfiniteScroll = false
                this.snackbar.text = 'Search results successfully copied to clipboard!';
                this.snackbar.color = 'success';
            } catch {
                this.snackbar.text = 'Something went wrong â€” nothing copied!';
                this.snackbar.color = 'error';
            }
            this.snackbar.isVisible = true;
        },
    },
}
</script>

<style scoped lang="scss">
.searcher-wrapper {
    display: flex;
    justify-content: center;
}

::v-deep .v-snack__wrapper {
    margin-top: 20px;
    margin-left: 70px;

    .v-snack__content {
        font-size: 20px;
        padding: 20px 25px;
    }
}
</style>
